:root {
  --primarySiteColor: #2ac2c7;
  --secondarySiteColor: #f5cd84;
  --thirdSiteColor: #373cab;
  --fourthSiteColor: #4ab565;
  --fifthSiteColor: #a14573;
  --primaryOrangeColor: #ff971d;
  --primaryPurpleColor: #8c0aa9;
  --secondaryOrangeColor: #ffec73;
  --secondaryPurpleColor: #4d0180;
  --darkPurpleColor: #1b062e;
  --primaryRedColor: #eb1737;
  --lightGrey: #f5f5f5;
  --darkGrey: #414141;
  --mediumGrey: #9b9b9b;
  --mainWhite: #ffffff;
  --mainBlack: #020202;
  --softBlack: #1c1c1a;
  --mainTransition: all 0.3s ease-in-out;
  --mainSpacing: 0.3rem;
}

.App {
  text-align: center;
}

html,
body,
#app,
#app > div {
  width: 100% !important;
  margin: 0px !important;
  padding: 0px !important;
  height: 100% !important;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
